/*eslint-disable no-unused-vars*/
import React, {Component} from "react";
/*eslint-enable no-unused-vars*/
import {Button, Icon} from "react-mdl";

class Item extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.id,
      name: props.name,
      phone: props.phone
    };
    this.onRemove = props.onItemRemoved;
    // This binding is necessary to make `this` work in the callback
    this.handleRemove = this
      .handleRemove
      .bind(this);
  }

  handleRemove(e) {
    this
      .props
      .delete(this.props.groupId, e.target.id);
  }

  render() {
    const msg = escape(`${this.state.name} didn't arrive to school`);
    return (
      <div id={this.state.id} className="item">
        <p>Name: {this.state.name}</p>
        <span>
          <a href={`tel:${this.state.phone}`} rel="noopener">
            Call
          </a>{" "}
        </span>
        <span>
          <a
            href={`sms://972${this
            .state
            .phone
            .substring(1)}?body=${msg}`}
            rel="noopener">
            SMS
          </a>{" "}
        </span>
        <span>
          <a
            href={`https://api.whatsapp.com/send?phone=972${this
            .state
            .phone
            .substring(1)}&text=${msg}`}
            rel="noopener">
            Chat
          </a>
        </span>
        <Button onClick={this.handleRemove}>
          <Icon id={this.state.id} name="delete"/>
        </Button>
      </div>
    );
  }
}

export default Item;



