/*eslint-disable no-unused-vars*/
import React, {Component} from "react";
/*eslint-enable no-unused-vars*/
import Panel from "muicss/lib/react/panel";
import Appbar from "muicss/lib/react/appbar";
import Container from "muicss/lib/react/container";
import {Icon} from "react-mdl";
import AddItem from "./AddItem";
import Item from "./Item";
import {Link} from "react-router-dom";

export default class Items extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props.match.params.id,
      items: []
    };
    this.registerDone = this
      .registerDone
      .bind(this);
    this.handleDoneClicked = this
      .handleDoneClicked
      .bind(this);
    this.handleItemAdded = this
      .handleItemAdded
      .bind(this);
    this.handleRemove = this
      .handleRemove
      .bind(this);
    // } componentWillMount() {   this.props.fetch().then(groups => {
    // this.setState(prevState => ({       groups: groups     }));   });
  }

  handleItemAdded(item) {
    this
      .props
      .add(this.state.id, item);
    this.setState(prevState => ({
      items: prevState
        .items
        .concat(item)
    }));
  }

  componentWillMount() {
    this
      .props
      .fetch(this.state.id)
      .then(group => {
        this.setState(prevState => ({items: group.items}));
      });
    console.log(this.state.items)
  }

  registerDone(f) {
    this.setState({doneHandler: f});
  }

  handleDoneClicked() {
    this
      .state
      .doneHandler();
  }

  handleRemove(groupId, itemId) {
    this.setState(prevState => ({
      items: prevState
        .items
        .filter((e) => {
          return e.id !== itemId
        })
    }));
    this
      .props
      .delete(groupId, itemId);
  }

  render() {
    let itemsList = [];
    console.log(this.state.items)
    if (this.state.items !== undefined && this.state.items.length > 0) {
      itemsList = this
        .state
        .items
        .map(item => {
          return (
            <Panel key={item.id}>
              <Item
                id={item.id}
                name={item.name}
                phone={item.phone}
                delete={this.handleRemove}
                groupId={this.state.id}/>
            </Panel>
          );
        });
    }
    return (
      <Container>
        <Appbar>
          <Link to="/">
            <Icon name="home"/>
          </Link>
          <Icon name="done" onClick={this.handleDoneClicked}/>
        </Appbar>

        <AddItem
          onItemAdded={this.handleItemAdded}
          register={this.registerDone}
          id={this.props.id}/>
        <div className="items">{itemsList}</div>
      </Container>
    );
  }
}



