import React, {Component} from "react";
import "./App.css";
import About from "./About";
import Groups from "./Groups";
import Items from "./Items";
import db from "./db";
import {Switch, Route, BrowserRouter} from "react-router-dom";

export default class App extends Component {
  fetchGroups() {
    return db
      .groups
      .toArray();
  }

  addGroup(group) {
    db
      .groups
      .add(group);
  }

  deleteGroup(id) {
    db
      .groups
      .delete(id);
  }

  fetchGroup(id) {
    return db
      .groups
      .toArray()
      .then(g => g.find(gg => {
        return (gg.id = id);
      }));
  }

  addItem(groupId, item) {
    db
      .groups
      .toArray()
      .then(g => g.find(gg => {
        return (gg.id = groupId);
      }))
      .then(m => {
        m.items = m
          .items
          .concat(item);
        db
          .groups
          .put(m);
        //  .then(function(updated) {     if (updated) console.log("updated");     else
        // console.log(         "Nothing was updated - there were no groups with key:
        // ",         m.id,         m       );   });
      });
  }

  deleteItem(groupId, itemId) {
    db
      .groups
      .toArray()
      .then(g => g.find(gg => {
        return (gg.id = groupId);
      }))
      .then(m => {
        m.items = m
          .items
          .filter(function (el) {
            return el.id !== itemId;
          });
        db
          .groups
          .put(m);
      });
  }

  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route
            exact
            path="/"
            render={props => (<Groups
            {...props}
            fetch={this.fetchGroups}
            add={this.addGroup}
            delete={this.deleteGroup}/>)}/>
          <Route
            path="/group/:id"
            render={props => (<Items
            {...props}
            add={this.addItem}
            fetch={this.fetchGroup}
            delete={this.deleteItem}/>)}/>
          <Route path="/about" component={About}/>
          <Route
            path="*"
            render={props => (<Groups
            {...props}
            fetch={this.fetchGroups}
            add={this.addGroup}
            delete={this.deleteGroup}/>)}/>
        </Switch>
      </BrowserRouter>
    );
  }
}



